import Vue from 'vue';
import i18n from '../../../i18n';
import * as autosize from 'autosize';
import { formatTimeString } from '../../../../../misc/format-time-string';
export default Vue.extend({
    i18n: i18n('common/views/components/messaging-room.form.vue'),
    props: {
        user: {
            type: Object,
            requird: false,
        },
        group: {
            type: Object,
            requird: false,
        },
    },
    data() {
        return {
            text: null,
            file: null,
            sending: false
        };
    },
    computed: {
        draftId() {
            return this.user ? 'user:' + this.user.id : 'group:' + this.group.id;
        },
        canSend() {
            return (this.text != null && this.text != '') || this.file != null;
        },
        room() {
            return this.$parent;
        }
    },
    watch: {
        text() {
            this.saveDraft();
        },
        file() {
            this.saveDraft();
            if (this.room.isBottom()) {
                this.room.scrollToBottom();
            }
        }
    },
    mounted() {
        autosize(this.$refs.textarea);
        // 
        const draft = JSON.parse(localStorage.getItem('message_drafts') || '{}')[this.draftId];
        if (draft) {
            this.text = draft.data.text;
            this.file = draft.data.file;
        }
    },
    methods: {
        async onPaste(e) {
            const data = e.clipboardData;
            const items = data.items;
            if (items.length == 1) {
                if (items[0].kind == 'file') {
                    const file = items[0].getAsFile();
                    const lio = file.name.lastIndexOf('.');
                    const ext = lio >= 0 ? file.name.slice(lio) : '';
                    const formatted = `${formatTimeString(new Date(file.lastModified), this.$store.state.settings.pastedFileName).replace(/{{number}}/g, '1')}${ext}`;
                    const name = this.$store.state.settings.pasteDialog
                        ? await this.$root.dialog({
                            title: this.$t('@.post-form.enter-file-name'),
                            input: {
                                default: formatted
                            },
                            allowEmpty: false
                        }).then(({ canceled, result }) => canceled ? false : result)
                        : formatted;
                    if (name)
                        this.upload(file, name);
                }
            }
            else {
                if (items[0].kind == 'file') {
                    this.$root.dialog({
                        type: 'error',
                        text: this.$t('only-one-file-attached')
                    });
                }
            }
        },
        onDragover(e) {
            const isFile = e.dataTransfer.items[0].kind == 'file';
            const isDriveFile = e.dataTransfer.types[0] == 'mk_drive_file';
            if (isFile || isDriveFile) {
                e.preventDefault();
                e.dataTransfer.dropEffect = e.dataTransfer.effectAllowed == 'all' ? 'copy' : 'move';
            }
        },
        onDrop(e) {
            // 
            if (e.dataTransfer.files.length == 1) {
                e.preventDefault();
                this.upload(e.dataTransfer.files[0]);
                return;
            }
            else if (e.dataTransfer.files.length > 1) {
                e.preventDefault();
                this.$root.dialog({
                    type: 'error',
                    text: this.$t('only-one-file-attached')
                });
                return;
            }
            //#region 
            const driveFile = e.dataTransfer.getData('mk_drive_file');
            if (driveFile != null && driveFile != '') {
                this.file = JSON.parse(driveFile);
                e.preventDefault();
            }
            //#endregion
        },
        onKeypress(e) {
            if ((e.which == 10 || e.which == 13) && e.ctrlKey && this.canSend) {
                this.send();
            }
        },
        chooseFile() {
            this.$refs.file.click();
        },
        chooseFileFromDrive() {
            this.$chooseDriveFile({
                multiple: false
            }).then(file => {
                this.file = file;
            });
        },
        onChangeFile() {
            this.upload(this.$refs.file.files[0]);
        },
        upload(file, name) {
            this.$refs.uploader.upload(file, this.$store.state.settings.uploadFolder, name);
        },
        onUploaded(file) {
            this.file = file;
        },
        send() {
            this.sending = true;
            this.$root.api('messaging/messages/create', {
                userId: this.user ? this.user.id : undefined,
                groupId: this.group ? this.group.id : undefined,
                text: this.text ? this.text : undefined,
                fileId: this.file ? this.file.id : undefined
            }).then(message => {
                this.clear();
            }).catch(err => {
                console.error(err);
            }).then(() => {
                this.sending = false;
            });
        },
        clear() {
            this.text = '';
            this.file = null;
            this.deleteDraft();
        },
        saveDraft() {
            const data = JSON.parse(localStorage.getItem('message_drafts') || '{}');
            data[this.draftId] = {
                updatedAt: new Date(),
                data: {
                    text: this.text,
                    file: this.file
                }
            };
            localStorage.setItem('message_drafts', JSON.stringify(data));
        },
        deleteDraft() {
            const data = JSON.parse(localStorage.getItem('message_drafts') || '{}');
            delete data[this.draftId];
            localStorage.setItem('message_drafts', JSON.stringify(data));
        },
    }
});
